.userDetailsContainer{
    button{
        border: none;
        display: flex;
        align-items: center;
        gap: 1rem;
        background: none;
        margin-bottom: 32px;
        cursor: pointer;

        p {
            color: #545F7D;
        }
    }

    .userDetailsTop{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;
        
    .usersButton{
        display: flex;
        gap: 1rem;
        cursor: pointer;
        
        :nth-child(1){
            border: 1px solid #E4033B;
            color: #E4033B;
            letter-spacing: 1px;
            font-size: 12px;
            font-weight: 600;
            padding: 12px 16px;
            margin-bottom: 0;
            border-radius: 8px;
        }

        :nth-child(2){
            border: 1px solid var(--primaryColor);
            color: var(--primaryColor);
            letter-spacing: 1px;
            font-size: 12px;
            font-weight: 600;
            padding: 12px 16px;
            margin-bottom: 0;
            border-radius: 8px;
        }
    }
    
    .header {
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;
        color: var(--secondaryColor);
      }
    }

    .cardTopHeader{
        display: flex;
        align-items: center;
        padding: 30px;

        .usersTier{
            display: flex;
            flex-direction: column;
            gap: 4px;

            p {
                font-weight: 500;
            }
        }

        .customerName{
            display: flex;
            flex-direction: column;
            gap: 2px;

            :nth-child(1) {
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 28px;
                color: var(--secondaryColor);
              }

            :nth-child(2) {
                font-size: 14px;
            }
        }
        .userAvatar{
            height: 100px;
            width: 100px;
            background: #FBFBFB;
            border-radius: 100%; 
            margin-right: 20px;
        }

        .divider{
            border: 1px solid #8b909f31;
            height: 70px;
            margin: 0 30px;
        }
    }

    .activeTabs{
        margin-top: 30px;

        .userInformation{
            padding: 30px;
    
            .personalInformation{
    
                h4 {
                    font-size: 14px;
                    font-weight: 500;
                    color: var(--secondaryColor);
                }
    
                .details{
                    padding-top: 30px;
                    display: grid;
                    grid-template-columns: repeat(5, 1fr);
                    grid-column-gap: 10px;
                    width: 100%;
                    grid-row-gap: 30px;
    
                    .detailsStyle{
                        display: flex;
                        flex-direction: column;
                        gap: 6px;
    
                        span {
                            font-size: 10px;
                            font-weight: 400;
                        }
    
                        p {
                            font-weight: 500;
                            font-size: 13px;
                        }
                    }
                }
    
                .horizontalDivider{
                    width: 100%;
                    border: 1px solid #8b909f31;
                margin: 30px 0;
                }
            }
        }
    }
}

@media screen and (min-width: 767px) and (max-width: 1023px) { 
    .userDetailsContainer{
        .cardTopHeader{
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 30px;
            gap: 16px;
    
            .usersTier{
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 4px;
            }
    
            .customerName{
                display: flex;
                flex-direction: column;
                gap: 2px;
                align-items: center;
    
              }
    
            .divider{
               display: none;
            }
        }
    
        .activeTabs{
            .userInformation{       
                .personalInformation{ 
                   .details{
                        padding-top: 30px;
                        display: grid;
                        grid-template-columns: repeat(3, 1fr);
                        grid-column-gap: 20px;
                        width: 100%;
                        grid-row-gap: 20px;
                   }}
            }
        }
    } 
  }
  

